import React, { useState, Suspense } from 'react'
import { Redirect } from 'react-dom'
import { makeStyles } from '@material-ui/core/styles'
import { useForm } from 'react-hook-form'
import TextField from '@material-ui/core/TextField'
import InputAdornment from '@material-ui/core/InputAdornment'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import LinearProgress from '@material-ui/core/LinearProgress'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import Card from '@material-ui/core/Card'
import { logout } from '../../utils'

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    maxWidth: 300,
    margin: 'auto',
    marginTop: 50,
    minHeight: 1100,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  customCard: {
    maxWidth: 600,
    minWidth: 275,
    margin: 'auto',
    padding: 20
  },
  rootForm: {
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  progress: {
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(2),
    },
  },
  }));


export default function Initialize(props) {

    const classes = useStyles()
    const { register, handleSubmit, watch, errors } = useForm()
   
    const [initialized, setInit] = useState(false)
    const [initialSupply, setInitialSupply] = useState()
    const [tokenOwner, setTokenOwner] = useState()
    const [totalSupply, setTotalSupply] = useState()
    const [tokenName, setTokenName] = useState()
    const [tokenSymbol, setTokenSymbol] = useState()
    const [precision, setPrecision] = useState()

  
    const onSubmit = async (values) => {
        event.preventDefault()
        const { tokenName, tokenSymbol, precision, initialSupply } = values
        console.log('values', values)
     
        let finished = await window.contract.init({
                            name: tokenName,
                            symbol: tokenSymbol,
                            precision: precision,
                            initialSupply: initialSupply
                        }, process.env.DEFAULT_GAS_VALUE)
        console.log('finished', finished)
        if(finished) {
            let totalSupply = initialSupply
            setTokenName(tokenName)
            console.log(tokenName)
            setTokenSymbol(tokenSymbol)
            console.log(tokenSymbol)
            setPrecision(precision)
            console.log(precision)
            setTotalSupply(totalSupply)
            console.log(totalSupply)
            setInitialSupply(initialSupply)
            console.log(initialSupply)
            let tokenOwner = await window.contract.getOwner()
            console.log(tokenOwner)
            setTokenOwner(tokenOwner)
            console.log(tokenOwner)
            let init = await window.contract.getInit()
            if (init=='done') {
              setInit(true)
            }
            console.log('token created')
            console.log('initialized', initialized)
            console.log(tokenName, tokenSymbol, precision, initialSupply, totalSupply, tokenOwner)
        } else {
            console.log('failed token creation')
            setInit(false)
        } 
    }

    if(!done) {
      return <LinearProgress className="progress"/>
    } else if (!initialized) {
    
        return (      
               
        <Grid container spacing={3}>
         <Grid item xs={12}>
           <Paper className={classes.paper}>
             <Button type="primary" style={{ float: 'right' }} onClick={logout}>Sign out</Button>
               <div className={classes.root}>
                 <Card className={classes.customCard}>
                
        <Typography variant="h6" component="h2">Fungible Token Creator</Typography>
        <form className={classes.rootForm} noValidate autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
          <TextField
            id="token-name"
            variant="outlined"
            name="tokenName"
            label="Token Name"
            inputRef={register({
                required: true,
            })}
            placeholder="e.g. Vital Point Coin"
          />
            
          <TextField
            id="token-symbol"
            variant="outlined"
            name="tokenSymbol"
            label="Token Symbol"
            required={true}
            inputRef={register({
                required: true
            })}
            placeholder="e.g. VPC"
          />

          <TextField
            id="token-precision"
            variant="outlined"
            name="precision"
            label="Precision"
            placeholder="18"
            inputRef={register({
                required: true,
            })}
            InputProps={{
                endAdornment: <InputAdornment position="end">Decimals</InputAdornment>,
            }}
            
          />

          <TextField
          id="token-supply"
          variant="outlined"
          name="initialSupply"
          label="Initial Supply"
          placeholder="e.g. 1000000000"
          inputRef={register({
              required: true, 
          })}
          InputProps={{
            endAdornment: <InputAdornment position="end">Tokens</InputAdornment>,
            }}
          />
        <Button variant="contained" color="primary" type="submit">
              Submit
        </Button>
        <Button variant="contained" color="secondary" type="reset">
              Reset
        </Button>
        </form>
        </Card>
        </div>
            </Paper>
          </Grid>
        </Grid> 
    )
  } else {
    return (<Redirect to="/"/>)
  }

}